@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.4.0/css/all.min.css');
header,footer, main{
  width:1050px;
  margin: 0 auto;
}

.h_top{
  margin: 50px 0px 0px 15px;
  display: flex;

}
#search{
  border: 1px solid #721cc2;
  border-radius: 3px;
  height: 50px;
  padding:3px 40px 3px 10px;
  margin-left: 30px;
  width: 400px;
  outline: none;
  transform: translateX(50px);
}
.fa-magnifying-glass{
  font-size: 20px; 
  line-height: 50px;
  color: #721cc2;
  transform: translate(20px, 15px);
}
.img{
  transform: translate(120px, -15px);
}
.h_top > p{
  margin-left: 2px; line-height: 40px;
  transform: translateX(10px);
}
.h_top >p  span{color: #721cc2; font-weight: 600;}
.h_bottom{
  display: flex;
  justify-content: space-around;
  margin: 10px 5px;
}
.lnb{
  float: right;
  clear: both;
  text-align: center;
  width: 250px;
  font-size: 14px;
  transform: translateY(-30px);
}


.lnb ul li:not(:last-child){
  border-right: 1px #333 solid;
  padding-right: 14px;
}

footer{
  width: 100%;
}
footer > address{
  font-style: normal;
  text-align: center;
  margin-top: 20px;
  background-color: #777;
  color: #fff;
  line-height: 40px;
}