@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.3.0/css/all.min.css);

*{margin:0px;padding:0px;}
ul,li,ol{
  list-style: none;
}
.day_special ul{
  display: flex;
  justify-content: flex-end;
}
.day_wrap{
  transform: translate(100px, 100px);
}
.banner01{  width:1050px;}
.banner01 img{width: 100%;}
h3{
  text-align: center;
  font-size: 28px;
  margin: 40px 0px;
}
ul{
  margin:0 auto;
  width:98%;
  display: flex;
  gap: 10px;
}
ul li{
  position: relative;
  z-index: 10;
  width: 23%;
}
.img_wrap{
  width: 100%;
  height: 280px;
  position: relative;
  z-index: 10;
}
ul li img{
  width:100%;
  transition: .4s;
}
ul li img:hover{
  filter:brightness(1.1);
  transform :scale(1.1);
}

ul li p{
  line-height: 34px;
}
ul li p:first-child{
  position: relative;
  overflow: hidden;
  height: 260px;
  
}
ul li p:nth-child(2){
  font-weight: 600;
}
ul li p:nth-child(3){
  font-weight: 600;
  color: #f00;
}

ul li p:last-of-type{
  font-size: 14px;
  position: absolute;

}
main{
  padding: 20px 0px;
}

.sideBanner{
  width: 80px;
  height: 180px;
  position: fixed;
  right: 10px;top:40%;
  z-index: 10;
}
.sideBanner img{
  width: 100%;
}
.sideBanner >.btn{
  font-size: 14px;
  text-align: center;
  border: solid 1px #333;
  padding: 10px 0px;
  display: block;
  background-color: #f9f9f9;
}

.sideBanner a:last-of-type{
  transform: translateY(-1px);
}